import * as React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import SidebarComponent from '../../layouts/Sidebar';
import { Segment, Card, Image } from 'semantic-ui-react';
import MixListHoc from '@music/hocs/mix/ListHoc';
import { IState, IGenre } from '@music/interfaces';
import { SizeMe } from 'react-sizeme';
import { genreModel } from '@music/models';

class GenreList extends React.Component<any, any> {

  constructor(props: any) {
    super(props);
  }

  // public componentDidMount() {
  //   console.log('##E', this.props);
  // }

  public render() {
    return (
      <>
      <SizeMe>
        {({ size }) => (
          <SidebarComponent>
            <Segment style={{height: 'auto'}} className="toptracks-container">
            <Card.Group>
              {this.props.genres.filter((g: IGenre) => g.list).map((genre: IGenre) => (
                <Card fluid={!((size.width || 0) > 603)} centered={!((size.width || 0) > 603)} key={genre.id} link>
                  <Image onClick={this.go(genre)} src={genre.image} />
                  <Card.Content onClick={this.go(genre)}>
                    <Card.Header>{genre.name}</Card.Header>
                    <Card.Description>{genre.name}</Card.Description>
                  </Card.Content>
                </Card>
              ))}
            </Card.Group>
            </Segment>
          </SidebarComponent>
        )}
      </SizeMe>
      </>
    );
  }

  private go(genre: IGenre): () => any {
    return () => {
      this.props.history.push(genreModel.newInstance(genre).url());
    }
  }
}

const mapStateToProps = (state: IState) => {
  return {
    genres: state.genres.data
  }
};

const GenreListPage = connect(mapStateToProps)(withRouter(GenreList));

export default MixListHoc(GenreListPage);



